/* body */
body {
  margin: 0;
  box-sizing: border-box;
  background-color: bisque;
  box-sizing: border-box;
}
:root {
  --maroon: rgb(211, 15, 48);
  --orange: rgb(226, 102, 30);
  --yellow: #8d0047;
  --blue: #0c0386;
  --red: #900385;
}

.main-content {
  display: grid;
  grid-template-columns: 1fr 5fr 1fr;
}
body,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  /* font-family: "Dongle", sans-serif; */
  font-family: "Lato", sans-serif;
}
#header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
img {
  width: 100px;
}
.biggerjohny {
  width: 120px;
}
.word-table {
  width: 100%;
  /* border: 1px dashed rgb(148, 14, 14); */
  border-collapse: collapse;
  cursor: pointer;
  font-family: "Quicksand", sans-serif;
  border-radius: 5px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
th {
  height: 40px;
  font-size: 1.2rem;
  border: none;
  text-align: center;
  background-color: rgb(226, 102, 30);
  color: whitesmoke;
  border-radius: 5px;
}
td {
  /* border: 0.5px dashed rgb(95, 1, 17); */
  border-radius: 5px;
  /* vertical |horizontal */
  padding: 2% 0;
  text-align: center;
}
tr {
  font-size: 1.05rem;
  border-radius: 5px;
}
tr:nth-child(even) {
  background-color: rgb(243, 208, 165);
}
