.dlete-button {
  font-size: 0.5rem;
  cursor: pointer;
  color: rgb(65, 2, 12);
  padding-left: 5px;
  padding-right: 5px;
}
.dlete-button:hover {
  background-color: rgb(65, 2, 12);
  color: white;
}

.input-box {
  display: grid;
  font-size: 1.1rem;
  text-align: center;

  margin-bottom: 10px;
}
.input-box label {
  font-size: 1.6rem;
  font-family: "Dongle", sans-serif;
}
.submit-btn {
  margin-top: 10px;
  margin-bottom: 15px;
  cursor: pointer;
  font-size: 1.5rem;
  font-family: "Dongle", sans-serif;
  color: white;
  background-color: rgb(211, 15, 48);
  border-radius: 8px;
  box-shadow: 2.5px 2.5px 2.5px;
}
.submit-btn:hover {
  color: white;
}
.input-box input {
  font-size: 1.2rem;
  padding: 7px 0;
  width: 100%;
  border-radius: 5px;
  border: none;
  background-color: rgb(250, 241, 229);
  margin-left: 5px;
  margin-right: 5px;
  box-shadow: 1.5px 1.5px 1.5px;
}

.title-container {
  display: grid;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
}
.title-text {
  font-size: 1.4rem;
  margin-top: 10px;
  margin-bottom: 3px;
}
.title-sub-text {
  font-size: 0.9rem;
}
.footer-container {
  display: grid;
  -webkit-justify-content: center;
          justify-content: center;
  margin-top: 10px;
  margin-bottom: 10px;
  background-color: rgb(211, 15, 48);
  background-color: var(--maroon);
  padding-top: 2.5px;
  padding-bottom: 2.5px;
  border-radius: 5px;
}
.header-container {
  background-color: rgb(211, 15, 48);
  background-color: var(--maroon);

  border-radius: 5px;
}
.bulb-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  padding: 4px;
  margin-left: 8px;
}
.span {
  width: 10px;
  height: 10px;

  border-radius: 50%;
  margin-left: 2px;
  box-shadow: 1px 1px 1px;
}
.light-1 {
  background-color: rgb(211, 15, 48);
  background-color: var(--maroon);
}
.light-2 {
  background-color: rgb(211, 15, 48);
  background-color: var(--maroon);
}
.light-3 {
  background-color: rgb(211, 15, 48);
  background-color: var(--maroon);
}
.light-3 {
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-name: changeColor;
          animation-name: changeColor;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
}

.light-2 {
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-name: changeColor2;
          animation-name: changeColor2;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
}

@-webkit-keyframes changeColor {
  from {
    background-color: rgb(211, 15, 48);
    background-color: var(--maroon);
  }

  to {
    background-color: white;
  }
}

@keyframes changeColor {
  from {
    background-color: rgb(211, 15, 48);
    background-color: var(--maroon);
  }

  to {
    background-color: white;
  }
}
@-webkit-keyframes changeColor2 {
  from {
    background-color: white;
  }

  to {
    background-color: rgb(211, 15, 48);
    background-color: var(--maroon);
  }
}
@keyframes changeColor2 {
  from {
    background-color: white;
  }

  to {
    background-color: rgb(211, 15, 48);
    background-color: var(--maroon);
  }
}

