/* body */
@import url("https://fonts.googleapis.com/css2?family=Bakbak+One&family=Dongle:wght@300&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bakbak+One&family=Dongle:wght@300&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bakbak+One&family=Dongle:wght@300&family=Lato:ital,wght@1,400&family=Quicksand:wght@450&display=swap");
body {
  margin: 0;
  box-sizing: border-box;
  background-color: bisque;
  box-sizing: border-box;
}
:root {
  --maroon: rgb(211, 15, 48);
  --orange: rgb(226, 102, 30);
  --yellow: #8d0047;
  --blue: #0c0386;
  --red: #900385;
}

.main-content {
  display: grid;
  grid-template-columns: 1fr 5fr 1fr;
}
body,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  /* font-family: "Dongle", sans-serif; */
  font-family: "Lato", sans-serif;
}
#header {
  display: flex;
  justify-content: center;
}
img {
  width: 100px;
}
.biggerjohny {
  width: 120px;
}
.word-table {
  width: 100%;
  /* border: 1px dashed rgb(148, 14, 14); */
  border-collapse: collapse;
  cursor: pointer;
  font-family: "Quicksand", sans-serif;
  border-radius: 5px;
  user-select: none;
}
th {
  height: 40px;
  font-size: 1.2rem;
  border: none;
  text-align: center;
  background-color: rgb(226, 102, 30);
  color: whitesmoke;
  border-radius: 5px;
}
td {
  /* border: 0.5px dashed rgb(95, 1, 17); */
  border-radius: 5px;
  /* vertical |horizontal */
  padding: 2% 0;
  text-align: center;
}
tr {
  font-size: 1.05rem;
  border-radius: 5px;
}
tr:nth-child(even) {
  background-color: rgb(243, 208, 165);
}
/* tr:hover {
  border: 1px solid rgb(95, 1, 17);
  border-radius: 7px;
  /* box-shadow: 2.5px 2.5px 2.5px; */

.dlete-button {
  font-size: 0.5rem;
  cursor: pointer;
  color: rgb(65, 2, 12);
  padding-left: 5px;
  padding-right: 5px;
}
.dlete-button:hover {
  background-color: rgb(65, 2, 12);
  color: white;
}

.input-box {
  display: grid;
  font-size: 1.1rem;
  text-align: center;

  margin-bottom: 10px;
}
.input-box label {
  font-size: 1.6rem;
  font-family: "Dongle", sans-serif;
}
.submit-btn {
  margin-top: 10px;
  margin-bottom: 15px;
  cursor: pointer;
  font-size: 1.5rem;
  font-family: "Dongle", sans-serif;
  color: white;
  background-color: rgb(211, 15, 48);
  border-radius: 8px;
  box-shadow: 2.5px 2.5px 2.5px;
}
.submit-btn:hover {
  color: white;
}
.input-box input {
  font-size: 1.2rem;
  padding: 7px 0;
  width: 100%;
  border-radius: 5px;
  border: none;
  background-color: rgb(250, 241, 229);
  margin-left: 5px;
  margin-right: 5px;
  box-shadow: 1.5px 1.5px 1.5px;
}

.title-container {
  display: grid;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.title-text {
  font-size: 1.4rem;
  margin-top: 10px;
  margin-bottom: 3px;
}
.title-sub-text {
  font-size: 0.9rem;
}
.footer-container {
  display: grid;
  justify-content: center;
  margin-top: 10px;
  margin-bottom: 10px;
  background-color: var(--maroon);
  padding-top: 2.5px;
  padding-bottom: 2.5px;
  border-radius: 5px;
}
.header-container {
  background-color: var(--maroon);

  border-radius: 5px;
}
.bulb-container {
  display: flex;
  justify-content: flex-start;
  padding: 4px;
  margin-left: 8px;
}
.span {
  width: 10px;
  height: 10px;

  border-radius: 50%;
  margin-left: 2px;
  box-shadow: 1px 1px 1px;
}
.light-1 {
  background-color: var(--maroon);
}
.light-2 {
  background-color: var(--maroon);
}
.light-3 {
  background-color: var(--maroon);
}
.light-3 {
  animation-duration: 1s;
  animation-name: changeColor;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

.light-2 {
  animation-duration: 1s;
  animation-name: changeColor2;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

@keyframes changeColor {
  from {
    background-color: var(--maroon);
  }

  to {
    background-color: white;
  }
}
@keyframes changeColor2 {
  from {
    background-color: white;
  }

  to {
    background-color: var(--maroon);
  }
}

